import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export let store = new Vuex.Store({
  state: {
    // 상태데이터
    name : '존슨',
    age : 28,
    likes : 100,
    isLikes : false
  },
  getters : {
    // 데이터를 가져오는 함수
    NAME(state) {
      return state.name;
    },
    GET_LIKES(state) {
      return state.likes;
    }
  },
  mutations: {
    // 데이터 수정 것
    CHANGE_NAME(state, 이름) {
      state.name = 이름
    },
    ADD_LIKES(state, 데이터) {
      state.likes++
    }
  },
  actions : {
    CHANGE_NAME(context, 데이터) {
        // Ajax를 이용하여 데이터를 변경해야함.
        context.commit( "CHANGE_NAME", 데이터);
    }
  }  
})
