<template>
    <div 
        v-bind:class="['filter-item', 필터]" 
        v-bind:style="{backgroundImage : `url( ${imagePath})`}"
        v-on:click="clickBox"    
    >
        <slot></slot>
    </div>
</template>

<script>
import EventBus from './../EventBus.js';
export default {
    name : 'filterBox',
    props : {
        imagePath : String,
        필터 : String
    },
    methods : {
        clickBox() {
            EventBus.$emit('select-filter', this.필터);
        }
    }
}
</script>

<style>
.filter-item {
width: 100px;
height: 100px;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background : black;
background-size: cover;
background-position: center;
}
</style>
