<template>
    <div class="body">

        <!-- <button v-on:click="$emit('hyodo', '50000')">데이터를 전해줘!</button> -->
        
        <div v-if="step == 1">
            <!-- <Post v-bind:포스팅="포스팅들[0]"/>
            <Post v-bind:포스팅="포스팅들[1]"/>
            <Post v-bind:포스팅="포스팅들[2]"/> -->
            <Post v-for="(포스팅,i) in 포스팅들" :key="i" v-bind:포스팅="포스팅" />
        </div>
        <div v-if="step == 2">
            <div v-bind:class="['upload-image', 선택한필터]" 
            v-bind:style="{backgroundImage:'url(' + imagePath + ')'}"
            ></div>
            <div class="filters">
                <FilterBox 
                v-for="(필터, i) in 필터목록" 
                v-bind:필터="필터" 
                v-bind:key="i" 
                v-bind:imagePath="imagePath">
                <p>{{ 필터 }}</p> <!-- slot의 기능 -->
                </FilterBox>
            </div>
        </div>
        <div v-if="step == 3">
            <!--<div class="upload-image" v-bind:style="{backgroundImage:'url(' + imagePath + ')'}"></div>-->
            <div class="upload-image" v-bind:style="{backgroundImage:'url(' + imagePath + ')'}"></div>
            <div class="write">
                <textarea class="write-box" v-on:input="$emit('Contents', $event.target.value)"></textarea>
            </div>
        </div>
    </div>
</template>

<script>
import Post from './Post.vue'
import FilterBox from './FilterBox.vue'

export default {
  name: 'Body',
  data() {
      return {
          필터목록 : [ "normal", "clarendon", "gingham", "moon", "lark", "reyes", "juno", "slumber", "aden", "perpetua", "mayfair", "rise", "hudson", "valencia", "xpro2", "willow", "lofi", "inkwell", "nashville"]
      }
  },
  props: {
      포스팅들 : Array,
      step : Number,
      imagePath : Object,
      선택한필터 : String,
  },
  components: {
    Post,
    FilterBox
  }
}
</script>

<style>
.upload-image{
width: 100%;
height: 350px;
background: cornflowerblue;
}
.filters{
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}

</style>
