<template>
  <div id="app">
    <!--<img alt="Vue logo" src="./assets/logo.png">-->
    <div class="header">
      <ul class="header-button-left">
        <li>Cancel</li>
      </ul>
      <ul class="header-button-right" v-if="step == 2" v-on:click="fncNext">
        <li>Next</li> 
      </ul>
      <ul class="header-button-right" v-if="step == 3" v-on:click="fncPublish">
        <li>발행</li> 
      </ul>
      <img src="./assets/logo.png" class="logo">
    </div>

    <Body 
      v-bind:imagePath="imagePath" 
      v-bind:step="step" 
      v-bind:포스팅들="포스팅들" 
      v-on:Contents="내가쓴글=$event"
      v-bind:선택한필터="선택한필터"
    />

    <button v-on:click="more">더보기</button>
    서바API : /more.json {테이터}

    <p>{{ $store.getters.NAME }}</p>
    <button v-on:click="$store.dispatch('CHANGE_NAME', '재홍')">버튼</button>

    <!-- <div class="sample-box"></div> -->
    <div class="footer">
      <ul class="footer-button-plus">
        <input type="file" id="file" class="inputfile" v-on:change="uploadImage">
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>

    업로드 버튼을 누르면..?
    1. 필터선택화면으로 넘어감
    2. 업로드한 사진을 저기 필터선택화면에서 크게 보여줌
    3. 
    new FileReader() -> HTML5에서 파일의 정보를 서버에 전송하기전에 할 수 있음
    

  </div>
</template>

<script>
import Body from './components/Body.vue'
import Data from './assets/postdata.js'
import EventBus from './EventBus.js'
import axios from 'axios';

export default {
  name: 'app',
  data() {
    return {
      포스팅들 : Data, // [{},{},{}]
      step : 1,
      imagePath : '',
      내가쓴글 : '',
      선택한필터 : ''
    }
  },
  components: {
    Body
  },
  mounted() {
    EventBus.$on('select-filter', (받은자료) => {
      //console.log(필터);
      this.선택한필터 = 받은자료;
    });
  },
  methods: {
    more() {
      axios.get('/more.json')
      .then(결과 => {
          console.log(결과.data);
          this.포스팅들.push(결과.data);
      }).catch(에러 => {
        console.log(에러)
      })
    },
    uploadImage(e) {
      let file = e.target.files;
      let reader = new FileReader();
      reader.readAsDataURL(file[0]);
      reader.onload = e => {
        //console.log(e.target.result);
        this.step = 2;
        this.imagePath = e.target.result;
      }
    },
    fncNext() {
      this.step = this.step + 1;
    },
    fncPublish() {
      // 글, 이미지 발행
      // 1. 메인화면으로 돌아감
      this.step = 1;

      // 2. 우리의 이미지와 글로 {}를 만들어서 [포스팅들]이라는 데이터에 추가
      let 내데이터 = {
        name: "Kimhyukmin",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: this.imagePath,
        likes: 36,
        date: 'May 15',
        liked: false,
        caption: this.내가쓴글,
        filter: "perpetua"
      }
      this.포스팅들.unshift(내데이터);
    }
  }
}
</script>

